"use client";

import dynamic from "next/dynamic";

// Dynamically import Lanyard to avoid SSR issues with Three.js
const Lanyard = dynamic(() => import("./lanyard"), { ssr: false });

export function FoundersSection() {
  return (
    <section className="w-full py-0 md:py-10 relative overflow-hidden">
      {/* Background Image */}
      <div
        className="absolute inset-0"
        style={{
          backgroundImage: "url(/founder_bg.jpg)",
          backgroundPosition: "center",
          backgroundSize: "cover",
          backgroundRepeat: "no-repeat",
        }}
      />

      {/* Dark Filter */}
      <div
        className="absolute inset-0"
        style={{
          backgroundColor: "rgba(0, 0, 0, 0.4)",
        }}
      />

      {/* Blending Overlay */}
      <div
        className="absolute inset-0"
        style={{
          background: "linear-gradient(135deg, rgba(65, 73, 135, 0.5) 0%, rgba(255, 255, 255, 0.3) 100%)",
          mixBlendMode: "multiply",
          opacity: 0.1,
        }}
      />

      {/* Content */}
      <div className="relative z-10">
        {/* Title */}
        <div className="text-center mb-5 md:mb-5">
        <h2 className="text-5xl md:text-7xl lg:text-8xl font-bold text-white font-[family-name:var(--font-montserrat)]">
          CO-FOUNDED BY
        </h2>
      </div>

      {/* Two Column Layout */}
      <div className="max-w-7x1 mx-0 px-5">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8 md:gap-12 lg:gap-16">
          {/* Left: Gayathra Bandara */}
          <div className="flex flex-col items-center">
            <div className="w-full h-[500px]">
              <Lanyard cardImage="/Gayathar.jpeg" />
            </div>
            <div className="text-center mt-6 space-y-2">
              <h3 className="text-2xl md:text-3xl font-bold text-white font-[family-name:var(--font-open-sans)]">
                Gayathra Bandara
              </h3>
              <p className="text-lg italic text-white  font-[family-name:var(--font-open-sans)]">
                Sri Lankan Scientist
              </p>
              <p className="text-sm md:text-base text-white leading-relaxed max-w-md mx-auto font-[family-name:var(--font-open-sans)]">
                Pioneering research in marine biology, Gayathra brings a
                scientific rigor to our mission, ensuring every initiative is
                grounded in data and ecological preservation.
              </p>
            </div>
          </div>

          {/* Right: Delphine Mestdagh */}
          <div className="flex flex-col items-center">
            <div className="w-full h-[500px]">
              <Lanyard cardImage="/Delphine.jpeg" />
            </div>
            <div className="text-center mt-6 space-y-2">
              <h3 className="text-2xl md:text-3xl font-bold text-white font-[family-name:var(--font-open-sans)]">
                Delphine Mestdagh
              </h3>
              <p className="text-lg italic text-white font-[family-name:var(--font-open-sans)]">
                Belgian Artist
              </p>
              <p className="text-sm md:text-base text-white leading-relaxed max-w-md mx-auto font-[family-name:var(--font-open-sans)]">
                Blending color and emotion, Delphine translates complex ocean
                narratives into visual stories that captivate hearts and inspire
                action across borders.
              </p>
            </div>
          </div>
        </div>
      </div>
      </div>
    </section>
  );
}
